.markers {
	&__list {
		display: flex;
		justify-content: space-between;
		flex: 1 1 100%;
		max-width: 1400px; }
	&__item {
		display: flex;
		align-items: center;
		margin: 0 10px;
		cursor: pointer;
		.no-touch &:hover &-text {
			color: $crimson;
			text-decoration: underline; }
		&-pic {
			display: block;
			margin-right: 15px;
			&_child-garden {
				@include s('child-garden'); }
			&_school {
				@include s('school'); }
			&_shopping {
				@include s('shopping'); }
			&_food {
				@include s('food'); }
			&_coffee {
				@include s('coffee'); }
			&_theatre {
				@include s('theatre'); }
			&_rest {
				@include s('rest'); } }
		&-text {
			color: $grey;
			font-size: 18px; } }
	&_view {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		z-index: 10;
		display: flex;
		justify-content: center;
		height: 110px;
		background: $white; } }
